.siglas__matriz h1 {
  font-size: 90px;
  margin: 0;
  margin-top: -15px;
  padding-left: 10px;
}

.siglas__matriz .f {
  padding-left: 25px;
  padding-right: 10px;
}

.strengths {
  margin: 20px auto;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  box-shadow: 0 0 15px 1px rgba(0, 0, 0, 0.4);
  padding: 20px;
  width: 70%;
}

.strengths__content {
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: space-around;
}

.img__strengths {
  width: 20%;
  text-align: center;
}

.img__strengths img {
  width: 40%;
}

.text__strengths {
  width: 70%;
}

.link__strengths {
  width: 10%;
}

.button__strengths {
  text-decoration: none;
  width: 100%;
  background: #cf1a29;
  font-weight: 400;
  color: white;
  border: 0 none;
  border-radius: 1px;
  cursor: pointer;
  padding: 10px 25px;
  margin: 10px 5px;
  border-radius: 8px;
  outline: none;
}

.button__strengths:hover,
.button__strengths:focus {
  box-shadow: 0 0 0 2px white, 0 0 0 3px #cf1a29;
}

.button__delete {
  text-decoration: none;
  width: 50%;
  background: #cf1a29;
  font-weight: 400;
  color: white;
  border: 0 none;
  border-radius: 1px;
  cursor: pointer;
  margin: 10px 5px;
  border-radius: 8px;
  outline: none;
}

.button__delete:hover,
.button__delete:focus {
  box-shadow: 0 0 0 2px white, 0 0 0 3px #cf1a29;
}

.button__edit {
  text-decoration: none;
  width: 50%;
  background: #1dad0a;
  font-weight: 400;
  color: white;
  border: 0 none;
  border-radius: 1px;
  cursor: pointer;
  padding: 10px 15px;
  margin: 10px 5px;
  border-radius: 8px;
  outline: none;
  text-align: center;
}

.button__edit:hover,
.button__edit:focus {
  box-shadow: 0 0 0 2px white, 0 0 0 3px #1dad0a;
  text-decoration: none;
  color: #fff;
}

p {
  margin: 0;
}
