.container__grafic {
  width: 1300px;
  margin: auto;
}

.main__grafic-foda {
  margin: 2.5rem 0;
}

.title__estrategi {
  margin-bottom: 50px;
}

.grid {
  display: grid;
  grid-template-areas:
    "blank grid1 grid2"
    "grid3 main main"
    "grid4 main main";
  align-items: center;
}

.blank1 {
  grid-area: blank;
  height: 50px;
  border-bottom: 2px solid #000;
  border-right: 2px solid #000;
}

.grid11 {
  grid-area: grid1;
  height: 50px;
  text-align: center;
  border-bottom: 2px solid #000;
  border-right: 2px solid #000;
}

.grid22 {
  grid-area: grid2;
  height: 50px;
  text-align: center;
  border-bottom: 2px solid #000;
}

.blank2 {
  grid-area: blank;
  border-right: 2px solid #000;
}

.grid33 {
  height: 100%;
  grid-area: grid3;
  border-right: 2px solid #000;
  border-bottom: 2px solid #000;
  display: flex;
  justify-content: center;
  align-items: center;
}

.grid44 {
  height: 100%;
  grid-area: grid4;
  border-right: 2px solid #000;
  display: flex;
  justify-content: center;
  align-items: center;
}

.main {
  grid-area: main;
}

.grafic__matriz-content-estrategia {
  width: 50%;
  height: 100%;
}

.grafic__foda-estrategia {
  display: flex;
  justify-content: center;
  margin: 1rem 1rem;
  padding: 0;
}

.link__action {
  text-decoration: none;
  width: 100%;
  background: #f53707;
  font-weight: 400;
  color: white;
  border: 0 none;
  border-radius: 1px;
  cursor: pointer;
  padding: 10px 25px;
  margin: 10px 5px;
  border-radius: 8px;
  outline: none;
  text-align: center;
}

.link__action:hover,
.link__action:focus {
  box-shadow: 0 0 0 2px white, 0 0 0 3px #f53707;
  text-decoration: none;
  color: #fff;
}
