.fill__matriz {
  margin: 4rem auto;
}

.line__separed {
  border-bottom: 2px solid #cf0718fd;
  margin-bottom: 15px;
}

.check-list {
  position: absolute;
  left: 20px;
}

.form-group {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.form-group input {
  padding-left: 40px;
  width: 460px;
  margin: 0 10px;
}

.widt__form {
  margin: 0 auto;
  width: 100%;
}

.link__result {
  float: right;
  margin-bottom: 50px;
  margin-right: 90px;
}

.list__input {
  flex-wrap: wrap;
  display: flex;
  justify-content: center;
}

.form__group {
  width: 100%;
}

.card__form {
  border-radius: 8px;
  box-shadow: 0 0 15px 1px rgba(0, 0, 0, 0.4);
}

.title__form {
  text-align: center;
  margin: 0;
  padding-bottom: 1.2rem;
}

.button__save {
  text-decoration: none;
  width: 100%;
  background: #1864f3;
  font-weight: 400;
  color: white;
  border: 0 none;
  border-radius: 1px;
  cursor: pointer;
  padding: 10px 25px;
  margin: 10px 5px;
  border-radius: 8px;
  outline: none;
}

.button__save:hover,
.button__save:focus {
  box-shadow: 0 0 0 2px white, 0 0 0 3px #1864f3;
}

@media (min-width: 768px) {
  .col-md-6 {
    flex: 0 0 50%;
    max-width: 70%;
  }
}
