.container__list-matriz {
  margin: 70px 100px;
}

.list__matriz {
  display: flex;
  justify-content: space-between;
  margin: 2rem 0;
  padding: 0;
}

.card__list {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}

.card__list img {
  margin-right: 10px;
}

.list__matriz-content {
  width: 24%;
  height: 100%;
}

.title__header {
  margin: 0;
  text-align: center;
  font-size: 20px;
}

.card__content {
  border-radius: 3px;
  box-shadow: 0 0 8px 1px rgba(0, 0, 0, 0.4);
}

.button__delete {
  text-decoration: none;
  width: 100%;
  background: #1864f3;
  font-weight: 400;
  color: white;
  border: 0 none;
  border-radius: 1px;
  cursor: pointer;
  padding: 10px 25px;
  margin: 10px 5px;
  border-radius: 8px;
  outline: none;
}

.button__delete:hover,
.button__delete:focus {
  box-shadow: 0 0 0 2px white, 0 0 0 3px #1864f3;
}

.button__edit {
  text-decoration: none;
  width: 100%;
  background: #1864f3;
  font-weight: 400;
  color: white;
  border: 0 none;
  border-radius: 1px;
  cursor: pointer;
  padding: 10px 25px;
  margin: 10px 5px;
  border-radius: 8px;
  outline: none;
}

.button__edit:hover,
.button__edit:focus {
  box-shadow: 0 0 0 2px white, 0 0 0 3px #1864f3;
}
