.container__grafic {
  width: 1300px;
  margin: auto;
}

.main__grafic-foda {
  margin: 2.5rem 0;
}

.grid {
  display: grid;
  grid-template-areas:
    "blank grid1 grid2"
    "grid3 main main"
    "grid4 main main";
  align-items: center;
}

.blank {
  grid-area: blank;
  height: 50px;
  border-bottom: 2px solid #000;
}

.grid1 {
  grid-area: grid1;
  height: 50px;
  text-align: center;
  border-bottom: 2px solid #000;
  border-right: 2px solid #000;
}

.grid2 {
  grid-area: grid2;
  height: 50px;
  text-align: center;
  border-bottom: 2px solid #000;
}

.blank {
  grid-area: blank;
  border-right: 2px solid #000;
}

.grid3 {
  height: 100%;
  grid-area: grid3;
  border-right: 2px solid #000;
  border-bottom: 2px solid #000;
  display: flex;
  justify-content: center;
  align-items: center;
}

.grid4 {
  height: 100%;
  grid-area: grid4;
  border-right: 2px solid #000;
  display: flex;
  justify-content: center;
  align-items: center;
}

.main {
  grid-area: main;
}

.grafic__foda {
  display: flex;
  justify-content: space-between;
  margin: 1rem 1rem;
  padding: 0;
}

.grafic__matriz-content {
  width: 50%;
  height: 100%;
}

.grafic__content {
  border-radius: 0px;
}

